import React from 'react'
import Router from 'next/router'
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { fetchDetails } from '../../services/swapi'

export default class DetailsPerson extends React.Component{

    static async getInitialProps({ query}) {
        const results = await fetchDetails( query.p)
            

            return {
                results
            }
        }
    render() {
        return (
            <Container>
                            
                <Table compact celled>
                    <Table.Header>
                    
                        <Table.Row>
                             <Table.HeaderCell> name </Table.HeaderCell>
                             <Table.HeaderCell> height </Table.HeaderCell>
                             <Table.HeaderCell> mass </Table.HeaderCell>
                             <Table.HeaderCell> hair color </Table.HeaderCell>
                             <Table.HeaderCell> skin color </Table.HeaderCell>
                             <Table.HeaderCell> eye_color </Table.HeaderCell>
                             <Table.HeaderCell> birth_year </Table.HeaderCell>
                             <Table.HeaderCell> gender </Table.HeaderCell>
                             <Table.HeaderCell> homeworld </Table.HeaderCell>
                             <Table.HeaderCell>films </Table.HeaderCell>
                             <Table.HeaderCell> species</Table.HeaderCell>
                             <Table.HeaderCell> vehicles </Table.HeaderCell>
                             <Table.HeaderCell> starships </Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>

                    
                    <Table.Body>
                   
                        <Table.Row >
                            <Table.Cell> {this.props.results.name} </Table.Cell>  
                            <Table.Cell> {this.props.results.height} </Table.Cell>  
                            <Table.Cell> {this.props.results.mass} </Table.Cell>  
                            <Table.Cell> {this.props.results.hair_color}  </Table.Cell>  
                            <Table.Cell> {this.props.results.skin_color} </Table.Cell>  
                            <Table.Cell> {this.props.results.eye_color} </Table.Cell>  
                            <Table.Cell> {this.props.results.birth_year} </Table.Cell>  
                            <Table.Cell> {this.props.results.gender} </Table.Cell>  
                            <Table.Cell> {this.props.results.homeworld} </Table.Cell>  
                            <Table.Cell> {this.props.results.films} </Table.Cell>  
                            <Table.Cell> {this.props.results.species} </Table.Cell>  
                            <Table.Cell> {this.props.results.vehicles} </Table.Cell>  
                            <Table.Cell> {this.props.results.starships} </Table.Cell>  
                         

                        </Table.Row>
                        
                    </Table.Body>
                </Table>                  
            </Container>
        )
    }
}





